@import './var';

* {
  font-family: 'Noto Sans TC', sans-serif;
}

html {
  position: relative;
  width: 100%;
  height: 100vh;
  margin: 0;
  white-space: nowrap;
  &::-webkit-scrollbar-track {
    // -webkit-box-shadow: inset 0 0 0 $darkBgColor;
    // background-color: $uerboardSideBarColor;
    background-color: transparent;
  }
  &::-webkit-scrollbar {
    width: 0px;
    height: 4px;
    background-color: transparent;
    // background-color: $uerboardSideBarColor;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $greenTextColor;
  }
}

* {
  // scroll bar
  &::-webkit-scrollbar-track {
    background-color: transparent;
  }
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
    background-color: transparent;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $greenTextColor;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  // height: 100vh;
  height: 100%;
  background-color: $mainBgColor;
  font-size: 15px;
  color: $whiteColor;

  ::placeholder,
  ::-moz-placeholder,
  ::-webkit-input-placeholder {
    color: $btnGreyTextColor;
  }
  *:focus {
    outline: none;
  }
  pre,
  code,
  textarea {
    font-family: 'Noto Sans TC', sans-serif;
  }
}

#root {
  width: 100%;
  height: 100%;
}

.page {
  padding-top: 60px;
  height: calc(100vh - 60px);
}

.divider {
  @include flexCenter;
  width: 280px;
  height: 1px;
  background-color: #42484f;
  &:before {
    content: '';
    width: 15%;
    height: 5px;
    background-color: $greenTextColor;
  }
}

.btn {
  border-radius: 30px;
  padding: 8px;
  text-align: center;
  cursor: pointer;
}

.btn_gradient {
  @include btn-gradient-color;
  color: $uerboardTextBgColor;
}
